/*--------------------
       Loading
---------------------*/
.ui.tab.loading {
	position: relative;
	overflow: hidden;
	display: block;
	min-height: 250px; }

.ui.tab.loading * {
	position: relative !important;
	left: -10000px !important; }

.ui.tab.loading:before,
.ui.tab.loading.segment:before {
	position: absolute;
	content: '';
	top: 100px;
	left: 50%;
	margin: -1.25em 0em 0em -1.25em;
